import React from 'react'
import DishData, { getDishesMap } from '../Dish/DishData'

import './FullRecipePage.css'

const FullRecipePage = ({
	match,
	DishesMap = getDishesMap(DishData),
}) => {
	return (
      	<div>
			<div className="container">
				<div className="recipe-page">
					<p className="title-right-cont">{DishesMap[match.params.id].name[0]}</p>
					<div className="content">
						<div className="full-recipe">
							<img src={DishesMap[match.params.id].image[0]} alt=""/>
						</div>
						<div dangerouslySetInnerHTML={{__html:DishesMap[match.params.id].recipe}}>
						</div>
					</div>
					<div dangerouslySetInnerHTML={{__html:DishesMap[match.params.id].method}}>
					</div>
				</div> 
			</div> 
       	</div>
    )
}

export default FullRecipePage