import React from 'react';
import {Route} from 'react-router-dom'
import DishData from './Dish/DishData'
import DishList from './Dish/DishList'
import FullRecipePage from './FullRecipePage/FullRecipePage'
import DishFilter from './DishFilter/DishFilter'
import RecipesPage from './RecipesPage/RecipesPage'

const Main = () => {
    return (
            <div>
                <div className="row">
                    <Route path="/" exact render={()=>(<DishList/>)}
                    />
                   <Route path="/fullrecipe/:id" render={({
                        match,
                    })=>(
                    <FullRecipePage
                        match={match}
                    />)}/> 
                    <Route path="/salads" render={()=>DishData.filter((dish)=>dish.id === 1).map(({
                        id,
                        name,
                        image,
                        filterDish,
                    })=>(
                    <DishFilter
                        key={id}
                        id={id}
                        firstName={name[0]}
                        secondName={name[1]}
                        thirdName={name[2]}
                        fourthName={name[3]}
                        firstImage={image[0]}
                        secondImage={image[1]}
                        thirdImage={image[2]}
                        fourthImage={image[3]}
                        firstDish={filterDish[0]}
                        secondDish={filterDish[1]}
                        thirdDish={filterDish[2]}
                        fourthDish={filterDish[3]}
                    />))}/> 
                    <Route path="/maindishes" render={()=>DishData.filter((dish)=>dish.id === 2).map(({
                        id,
                        name,
                        image,
                        filterDish,
                    })=>(
                    <DishFilter
                        key={id}
                        id={id}
                        firstName={name[0]}
                        secondName={name[1]}
                        thirdName={name[2]}
                        fourthName={name[3]}
                        firstImage={image[0]}
                        secondImage={image[1]}
                        thirdImage={image[2]}
                        fourthImage={image[3]}
                        firstDish={filterDish[0]}
                        secondDish={filterDish[1]}
                        thirdDish={filterDish[2]}
                        fourthDish={filterDish[3]}
                    />))}/> 
                     <Route path="/desserts" render={()=>DishData.filter((dish)=>dish.id === 3).map(({
                        id,
                        name,
                        image,
                        filterDish,
                    })=>(
                    <DishFilter
                        key={id}
                        id={id}
                        firstName={name[0]}
                        secondName={name[1]}
                        thirdName={name[2]}
                        fourthName={name[3]}
                        firstImage={image[0]}
                        secondImage={image[1]}
                        thirdImage={image[2]}
                        fourthImage={image[3]}
                        firstDish={filterDish[0]}
                        secondDish={filterDish[1]}
                        thirdDish={filterDish[2]}
                        fourthDish={filterDish[3]}
                    />))}/> 
                    <Route path="/recipespage" render={()=>DishData.map(({
                        id,
                        title,
                        image,
                        name,
                        prep,
                        cook,
                        ready,
                    })=>(
                    <RecipesPage
                        id={id}
                        title={title}
                        firstName={name[0]}
                        secondName={name[1]}
                        thirdName={name[2]}
                        fourthName={name[3]}
                        firstImage={image[0]}
                        secondImage={image[1]}
                        thirdImage={image[2]}
                        fourthImage={image[3]}
                        prep1={prep[0]}
                        cook1={cook[0]}
                        ready1={ready[0]}
                        prep2={prep[1]}
                        cook2={cook[1]}
                        ready2={ready[1]}
                        prep3={prep[2]}
                        cook3={cook[2]}
                        ready3={ready[2]}
                    />))}/>  
                </div>
            </div>
    )
}

export default Main