        recipe: "<div class='description-dish'>A jibarito is a unique and original sandwich created by Puerto Rican immigrants at the Borinquen restaurant in Humboldt Park. Today it is served throughout the city. This particular jibarito recipe features roast beef topped with Swiss cheese, lettuce, tomato, and a spiced mayonnaise. You can make these full-sized and enjoy them as a meal or cut the plantains down and build them into small party sandwiches.<div class='title-ingr'>Ingredients:</div><div class='description-ingr'><p>2 tbspn oil</p><p>2 large plantains (green)</p><p>1/4 cup mayonnaise</p><p>1/4 teaspoon chili, cumin and garlic powder</p><p>4 handfuls lettuce</p><p>1 tomato (thinly sliced)</p><p>1 onion (peeled and cut into slivers)</p><p>4 slices Swiss cheese (sliced for sandwiches)</p><p>1/4 pound roast beef (thinly sliced)</p></div></div>",
